<template>
  <div>
    <h1>个人中心</h1>
    <van-image
      round
      width="8rem"
      height="8rem"
      src="https://img01.yzcdn.cn/vant/cat.jpeg"
    />
    <div class="userName">
      <p>{{user.name}}</p>
    </div>
    <p class="userId">账号：{{user.email}}</p>
  </div>

  <van-cell title="我的收藏" is-link @click="goto('/collect')" />
  <van-cell title="我的订单" is-link @click="goto('/order')" />
  <van-cell title="账号管理" is-link @click="goto('/setting')" />
  <van-cell title="地址管理" is-link @click="goto('/address')" />
  <van-cell title="关于我们" is-link @click="goto('/about')" />

  <div style="margin: 15px; margin-top: 80px">
    <van-button @click="tologout" block color="var(--color-tint)"
      >退出登录</van-button
    >
  </div>
</template>

<script>
import { logout, getuser } from "network/user";
import { Toast } from "vant";
import { useRouter } from "vue-router";
import { useStore } from "vuex";
import { onMounted, reactive, toRefs } from "vue";

export default {
  name: "Proflie",
  setup() {
    const store = useStore();
    const router = useRouter();

    const state = reactive({
      user: {},
    });

    onMounted(() => {
      getuser().then((res) => {
        state.user = res;
      });
    });

    const tologout = () => {
      logout().then((res) => {
        if (res.status == "204") {
          Toast.success("退出成功");
          // 清除token
          window.localStorage.setItem("token", "");
          store.commit("setIsLogin", false);
          setTimeout(() => {
            router.push({ path: "/login" });
          }, 500);
        }
      });
    };

    // 跳转方法
    const goto = (path,query) => {
      router.push({path,query:query || {}})
    }

    return {
      tologout,
      ...toRefs(state),
      goto,
    };
  },
};
</script>

<style>
.van-cell {
  margin-bottom: 10px;
  border-bottom: 1px solid #ebedf0;
  padding: 12px 16px;
  color: #323233;
  font-size: 14px;
  line-height: 24px;
  font-family: 14px -apple-system,BlinkMacSystemFont,Helvetica Neue,Helvetica,Arial,PingFang SC,Hiragino Sans GB,Microsoft YaHei,sans-serif;;
}
.userName {
  font-family:Verdana, Geneva, Tahoma, sans-serif;
  line-height: 4rem;
  height: 2rem;
  font-size: 26px;
  margin-bottom: 1.5rem;
}
.userId {
  font-size: 8px;
  margin-bottom: 2rem;
}
</style>